import logo from 'assets/chatImages/logo.png';
import API from 'mainAPI';
import { useCallback, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

import { TableTitle } from '@pages/EditPrivacy/styles';
import { Title } from '@pages/LogIn/styles';
import { Form } from '@pages/Question/styles';

import { Table } from '@components/Table';
import Button from '@components/atoms/Button';

import { SignUpWrap, WhiteBox, Error } from './styles';

function SignUp() {
  let navigate = useNavigate();

  const [mismatchError, setMismatchError] = useState(false);
  const [email, setEmail] = useState(undefined);
  const [password, setPassword] = useState(undefined);
  const [passwordCheck, setPasswordCheck] = useState(undefined);

  const onChangeEmail = useCallback(e => {
    setEmail(e.target.value);
  }, []);
  const onChangePassword = useCallback(
    e => {
      setPassword(e.target.value);
      setMismatchError(e.target.value !== passwordCheck);
    },
    [passwordCheck],
  );
  const onChangePasswordCheck = useCallback(
    e => {
      setPasswordCheck(e.target.value);
      setMismatchError(e.target.value !== password);
    },
    [password],
  );

  const onSubmit = useCallback(
    e => {
      e.preventDefault();
      if (!mismatchError && email && password) {
        console.log(' ');
        console.log(email, password);
        API.post('/users', {
          email,
          password,
          birthdate: '2022-10-29',
          phone_number: 'string',
        })
          .then(response => {
            navigate('/login', { replace: true });
          })
          .catch(error => {
            console.log(error);
          })
          .finally(() => {});
      }
    },
    [email, password, mismatchError, navigate],
  );

  return (
    <SignUpWrap>
      <Title onClick={() => navigate('/')}>
        <img style={{ marginTop: '20px', width: '300px' }} src={logo} alt="logo" />
      </Title>

      <WhiteBox>
        <div className="signupTitle"></div>

        <Form onSubmit={onSubmit}>
          <TableTitle></TableTitle>
          <Table>
            <tr>
              <th scope="row" className="border-bottom">
                
              </th>
              <td className="border-bottom">
                <input
                  id="email"
                  type="email"
                  name="email"
                  value={email}
                  onChange={onChangeEmail}
                  placeholder="+  8 "
                />
              </td>
            </tr>
            <tr>
              <th scope="row" className="border-bottom">
                
              </th>
              <td className="border-bottom">
                <input
                  type="password"
                  id="password"
                  name="password"
                  value={password}
                  onChange={onChangePassword}
                  placeholder="+  8 "
                />
              </td>
            </tr>
            <tr>
              <th scope="row" className="border-bottom">
                 
              </th>
              <td className="border-bottom">
                <input
                  type="password"
                  id="passwordCheck"
                  name="passwordCheck"
                  value={passwordCheck}
                  onChange={onChangePasswordCheck}
                  placeholder="+  8 "
                />
              </td>
            </tr>
            <tr>
              <th scope="row"> </th>
              <td>
                <button className="button-in-table"></button>
              </td>
            </tr>
          </Table>
          <TableTitle></TableTitle>
          <Table>
            <tr>
              <th scope="row" className="border-bottom">
                
              </th>
              <td className="border-bottom">
                <input className="input-middle" type="text" placeholder="" />
                <button className="button-in-table"></button>
              </td>
            </tr>
            <tr>
              <th scope="row"></th>
              <td>
                <input className="input-middle" type="text" placeholder="" />
                <button className="button-in-table"></button>
              </td>
            </tr>
          </Table>
          {mismatchError && <Error>  .</Error>}
          {!email && <Error> !</Error>}
          <Button
            buttonType="contained"
            color="yellow"
            radius="round"
            customCss={{
              padding: '15px 68px',
            }}>
            
          </Button>
        </Form>
        <div className="linkToLogin">
          <Link to="/login"></Link>
        </div>
      </WhiteBox>
    </SignUpWrap>
  );
}

export default SignUp;
